                        </div>
                      </div>
                    </li>
                    <li><router-link class="router_a" :class="{select : router_active[2]}" to="/about"></router-link>
                      <div class="tip inner-list">
                        <div class="inner"></div>
                        <div class="inner-list-list" style=" color: #fff;text-align: center; margin-top: 15px;">
                            <router-link to="/about/time"></router-link>
                            <router-link to="/about/searchPrice"></router-link>
                            <router-link to="/about/Know"></router-link>
                            <router-link to="/about/Loss"></router-link>
                        </div>
                      </div>
                    </li>
                    <li><router-link class="router_a" :class="{select : router_active[3]}" to="/security"></router-link></li>
                    <li><router-link class="router_a" :class="{select : router_active[4]}" to="/company"></router-link>
                      <div class="tip inner-list" style="height:150px; ">
                        <div class="inner"></div>
                        <div class="inner-list-list" style=" color: #fff;text-align: center; margin-top: 15px;">
                            <router-link to="/company/subwayCompany"></router-link>
                            <router-link to="/company/companyCulture"></router-link>
                            <router-link to="/company/eventRecord"></router-link>
                        </div>
                      </div>
                    </li>
                    <li><router-link class="router_a" :class="{select : router_active[5]}" to="/pac"></router-link>
                      <div class="tip inner-list" style="height:120px; ">
                        <div class="inner"></div>
                        <div class="inner-list-list" style=" color: #fff;text-align: center; margin-top: 15px;">
                            <router-link to="/pac/Plan"></router-link>
