                            <!-- <a href="###"></a> -->
                            <!-- <a href="###"></a> -->
                            <router-link to="/touch/say"></router-link>
                        </div>
                      </div>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
</template>

<script>
// import func from '../../../vue-temp/vue-editor-bridge'
export default {
  name: 'Header',
//   props: {
//     msg: String
//   }
  data() {
    return {
      router_active : [true],
      index : 0
    }
  },
  methods:{
    scrollHandle: function() {
      var header = document.getElementById('top');
      var router_a = document.getElementsByClassName('router_a');
      if(document.documentElement.scrollTop > 80) {
        header.style.backgroundColor = '#fff';
        for(var i = 0;i < router_a.length;i++) {
          router_a[i].style.color = '#000';
        }
      } else {
        header.style.backgroundColor = 'rgba(38, 48, 120, 0.3)';
        for(var j = 0;j < router_a.length;j++) {
          router_a[j].style.color = '#fff';
        }
      }
    },
    selected : function(e) {
      var li = document.getElementsByTagName('li');
      for(var i = 0;i < li.length;i++) {
        if(e.target.innerHTML === li[i].firstChild.innerHTML) {
          this.index = i;
          break;
        }
      }
      this.router_active = []
      for(var j = 0;j < 8;j++) {
        if(this.index == j) {
          this.router_active.push(true);
        }else {
          this.router_active.push(false);
        }
      }
    }
  },
  mounted() {
    window.addEventListener('scroll',this.scrollHandle)
  },
}
</script>

